<script>
import axios from 'axios';

export default {
  name: 'UserFriendlyAnalyzer',
  data() {
    return {
      // 20
      hotFunds: [
        { code: '000002', name: '', type: '', rating: 5 },
        { code: '000004', name: '', type: '', rating: 5 },
        { code: '000007', name: '', type: '', rating: 5 },
        { code: '005669', name: '', type: '', rating: 5 },
        { code: '005827', name: '', type: '', rating: 5 },
        { code: '006751', name: '', type: '', rating: 5 },
        { code: '161005', name: 'LOF', type: '', rating: 5 },
        { code: '110022', name: 'A', type: '', rating: 5 },
        { code: '260108', name: '', type: '', rating: 5 },
        { code: '001744', name: '', type: '', rating: 4 },
        { code: '001616', name: '', type: '', rating: 4 },
        { code: '000001', name: '', type: '', rating: 4 },
        { code: '000003', name: '', type: '', rating: 4 },
        { code: '000005', name: '', type: '', rating: 4 },
        { code: '000006', name: '', type: '', rating: 4 },
        { code: '000008', name: '', type: '', rating: 4 },
        { code: '000009', name: '', type: '', rating: 4 },
        { code: '000010', name: '', type: '', rating: 4 },
        { code: '001632', name: '', type: '', rating: 4 },
        { code: '003096', name: 'C', type: '', rating: 4 }
      ],
      
      // 
      investmentStyles: [
        {
          value: 'conservative',
          name: '',
          icon: '',
          description: '',
          riskLevel: 0.3
        },
        {
          value: 'balanced',
          name: '', 
          icon: '',
          description: '',
          riskLevel: 0.5
        },
        {
          value: 'aggressive',
          name: '',
          icon: '',
          description: '',
          riskLevel: 0.7
        }
      ],
      
      // 
      searchKeyword: '',
      searchResults: [],
      selectedFunds: [],
      selectedStyle: 'balanced',
      
      // 
      analyzing: false,
      analysisResult: null
    };
  },
  computed: {
    expectedReturn() {
      if (!this.analysisResult) return '0';
      const annualReturn = this.analysisResult.portfolioMetrics.annual_return;
      return (annualReturn * 100).toFixed(1);
    },
    maxDrawdown() {
      if (!this.analysisResult) return '0';
      const drawdown = this.analysisResult.portfolioMetrics.max_drawdown;
      return (Math.abs(drawdown) * 100).toFixed(1);
    },
    investmentStyleText() {
      const styleMap = {
        'conservative': '',
        'balanced': '', 
        'aggressive': ''
      };
      return styleMap[this.selectedStyle] || '';
    }
  },
  methods: {
    async searchFunds() {
      if (!this.searchKeyword.trim()) {
        this.searchResults = [];
        return;
      }
      
      try {
        const params = { keyword: this.searchKeyword, limit: 10 };
        const response = await axios.get('/api/funds/search', { params });
        
        if (response.data.status === 'success') {
          this.searchResults = response.data.funds;
        }
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    
    addFund(fund) {
      if (!this.selectedFunds.find(f => f.code === fund.code)) {
        this.selectedFunds.push(fund);
        this.searchResults = [];
        this.searchKeyword = '';
      }
    },
    
    removeFund(fundToRemove) {
      this.selectedFunds = this.selectedFunds.filter(fund => fund.code !== fundToRemove.code);
    },
    
    selectHotFund(fund) {
      if (!this.selectedFunds.find(f => f.code === fund.code)) {
        this.selectedFunds.push(fund);
      }
    },
    
    getFundName(fundCode) {
      const selectedFund = this.selectedFunds.find(f => f.code === fundCode);
      if (selectedFund) {
        return selectedFund.name;
      }
      
      const hotFund = this.hotFunds.find(f => f.code === fundCode);
      if (hotFund) {
        return hotFund.name;
      }
      
      return `${fundCode}`;
    },
    
    async startAnalysis() {
      if (this.selectedFunds.length === 0) {
        alert('');
        return;
      }
      
      try {
        this.analyzing = true;
        
        // 
        const fundPool = {};
        for (const fund of this.selectedFunds) {
          try {
            const response = await axios.get(`/api/funds/${fund.code}`);
            if (response.data.status === 'success') {
              const returns = response.data.nav_history.map(item => item.daily_return);
              fundPool[fund.code] = { returns };
            }
          } catch (error) {
            console.warn(` ${fund.code} :`, error);
          }
        }
        
        if (Object.keys(fundPool).length === 0) {
          throw new Error('');
        }
        
        // 
        const selectedStyleObj = this.investmentStyles.find(s => s.value === this.selectedStyle);
        const riskTolerance = selectedStyleObj ? selectedStyleObj.riskLevel : 0.5;
        
        // 
        const response = await axios.post('/api/analyze', {
          fund_pool: fundPool
        });
        
        if (response.data.status === 'success') {
          this.analysisResult = {
            portfolio: response.data.portfolio,
            portfolioMetrics: response.data.risk_report.risk_metrics,
            riskAssessment: response.data.risk_report.risk_assessment
          };
        } else {
          throw new Error(response.data.error);
        }
        
      } catch (error) {
        console.error(':', error);
        alert('');
      } finally {
        this.analyzing = false;
      }
    },
    
    resetAnalysis() {
      this.analysisResult = null;
      this.selectedFunds = [];
      this.selectedStyle = 'balanced';
    }
  }
};
</script>